
import BirdyService from '../BirdyService'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Box from 'vux/src/components/box/index.vue'


const baseList = [{
  url: 'javascript:',
  img: 'https://static.vux.li/demo/1.jpg',
  title: '送你一朵fua'
}, {
  url: 'javascript:',
  img: 'https://static.vux.li/demo/2.jpg',
  title: '送你一辆车'
}, {
  url: 'javascript:',
  img: 'https://static.vux.li/demo/3.jpg',
  title: '送你一次旅行'
}]

const urlList = baseList.map((item, index) => ({
  url: 'http://m.baidu.com',
  img: item.img,
  title: `(可点击)${item.title}`
}))

export default {
  components: {
    Swiper, SwiperItem, GroupTitle, XButton, Divider, Group, XInput, Selector, Box
  },
  data () {
    return {
      birdyService: new BirdyService(this),
      content: {},
      selected: 0,
      demo06_list: urlList,
      demo06_index: 0,
      name: '',
      value: '',
      loanAmountOptions: [
        { key: 1, value: '1万'},
        { key: 1, value: '1万'},
        { key: 1, value: '1万'},
        { key: 1, value: '1万'},
        { key: 1, value: '1万'},
        { key: 1, value: '1万'},
        { key: 1, value: '1万'},
        { key: 1, value: '1万'},
        { key: 1, value: '1万'},
        { key: 1, value: '1万'},
        { key: 1, value: '1万'},
        { key: 1, value: '1万'},
        { key: 1, value: '1万'}
      ]
    }
  },
  created () {
    this.pageInit()
  },
  methods: {
    demo06_onIndexChange (index) {
      this.demo06_index = index
    },
    pageInit () {
      this.birdyService.get(`/api/content/1?access-token=abc123_`).then((resp) => {
        this.content = resp
      }).catch((resp) => {
        console.log(resp)
      }).finally(() => {

      })
    },
    apply () {
      this.birdyService.create(`/api/content/1?access-token=abc123_`).then((resp) => {
        this.content = resp
      }).catch((resp) => {
        console.log(resp)
      }).finally(() => {

      })
    }
  }
}



